.App {
display: flex;
flex-direction: column;
width: 100vw;
height:100vh;
}

.AppHeader{
height: 50px;
display: flex;
justify-content: space-around;
color: rgb(78, 57, 30);
background-color: rgb(171, 245, 241);
align-items: center;
padding: 4px 20px 4px 12px;
border-bottom: 1px solid rgba(81, 175, 212, 0.15);
}
.AppFooter{
height:50px;
display: flex;
justify-content: space-evenly;
align-items: center;
border-top: 1px solid rgba(0,0,0,0.15);
background-color:rgb(48, 62, 70);

}
.AppHeader ul{
  color: aqua;
}

.SideMenuAndPageContent{
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: flex-start;

}

.SideMenu{

}
.PageContent{
  padding-left: 12px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
